.recipes-js {
  display: flex;
  flex-wrap: wrap;
  gap: 18px;
  justify-content: space-evenly;
}

div#text_from_above {
position: relative;
}

.text_from_above {
height: 240px;
position: absolute;
top: 15%;
left: 10px;
/* text-transform: uppercase; */
color: white;
background: transparent;
padding: 10px;
text-align: end;
}

.recipes_button_heart_from_above {
  padding-bottom: 90px;
  /* position: absolute; */
}

.recipes_button_heart {
  border: none;
  /* background-color: rgb(210, 216, 48); */
  background-color: transparent;
  gap: 14px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  color: #000;
  position: absolute;
  right: 16px;
  }

.recipes_button_heart:hover {
   fill : #000;
   /* background-color: rgb(210, 216, 48); */
}


.recipes_icon_heart {
  /* margin-right: 14px; */
  stroke: #000;
  /* fill: currentColor; */
}

.text_from_above_title {
   text-align: left;
}

.recipes_img {
  border-radius: 15px;
}

.recipes_title {  
  width: 180px;
  height: 18px;
   color: #F8F8F8;
   text-align: left;
   font-family: Inter;
   font-size: 14px;
   /* font-style: normal; */
   font-weight: 600;
   line-height: 1.29; 
   text-transform: uppercase;
   text-overflow: ellipsis;
   white-space: nowrap;
   overflow: hidden;
} 

.recipes_description {
  text-align: left;
   width: 208px;
   height: 32px;
   overflow: hidden;
   color: #F8F8F8;
   text-overflow: ellipsis;
   /* white-space: nowrap; */
   font-family: Inter;
   font-size: 12px;
   font-style: normal;
   font-weight: 400;
   line-height: 1.33; 
}

.recipes_rating {
   overflow: hidden;
   color: #F8F8F8;
   text-overflow: ellipsis;
   /* whitespace: nowrap; */
   font-family: Inter;
   font-size: 12px;
   /* ont-style: normal; */
   font-weight: 400;
   line-height: 1.33; 

}

