  text-align: left;
   width: 116px;
  height: 18px;
  align-content: center;
  font-size: 24px;
}

.recipes_btn_see_recipe {
    width: 93px;
    height: 31px;
    background-color: var(--primary-color-btn);
    margin-bottom: 16px;
    margin-right: 8px;
